.terms-conditions .section-title p{
    max-width: 100%;
    color: #636363;
    font-size: 16px;
}

.accordion-item{
    border-radius: 0 !important;
    margin-bottom: 30px;
    border: 0;
}
.accordion-button{
    background-color: #E8F2FF;
    font-size: 20px;
    font-weight: 600;
    color: #131313;
    box-shadow: none;
}

.accordion-button:focus{
    box-shadow: none;
    border-color: transparent;
    color: #131313;
}

.accordion-body{
    border: 1px solid #D9D9D9;
    border-top-color: transparent;
}
.accordion-body p{
    font-size: 16px;
    color: #636363;
    line-height: 24px;
    padding-bottom: 10px;
}